.overlay, .modal-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.overlay {
  z-index: 50;
  background: rgba(0, 0, 0, 0.6);
}

.modal-container {
  z-index: 75;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.modal {
  background: white;
  width: 650px;
  max-width: 100%;
  height: 500px;
  max-height: 100%;
  padding: 10px 20px;
}

.modal-header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.modal-body {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.close-button {
  cursor: pointer;
  background: transparent;
  border: 0;
  font-size: 25px;
}

.picker-button {
  cursor: pointer;
  border-radius: 4px;
  border: 0;
  font-size: 16px;
  text-decoration: underline;
  background: transparent;
  padding: 10px 20px;
  margin: 8px 5px;
}

.picker-button:hover {
  background: lightgray;
}


